<template>
    <p-fieldset>
        <template #legend> Current Patient: </template>

        <patient-message-log-form />

        <h4>Current Messge History</h4>

        <patient-message-log-history />

        <patient-message-log-register-dailog />
    </p-fieldset>
</template>

<script>
import PatientMessageLogForm from "@/components/patient-messgae-log/form.vue";
import PatientMessageLogHistory from "@/components/patient-messgae-log/history.vue";
import PatientMessageLogRegisterDailog from "@/components/patient-messgae-log/register-dialog.vue";

import { onMounted, provide, ref } from "vue";
import { emitter } from "@/event-bus";
export default {
    components: {
        PatientMessageLogForm,
        PatientMessageLogHistory,
        PatientMessageLogRegisterDailog
    },
    setup() {
        const selectedPaitent = ref();
        provide("selectedPaitent", selectedPaitent);

        const registerDialog = ref(false);
        provide("registerDialog", registerDialog);

        const isRegistered = ref(false);
        provide("isRegistered", isRegistered);

        const hasSetup = ref(false);
        provide("hasSetup", hasSetup);

        const messageHistories = ref([]);
        provide("messageHistories", messageHistories);

        onMounted(() => {
            emitter.emit("message-nofication:has-seen");
        });
    }
};
</script>
